// components/AddLaptopForm.tsx
import React, { useState } from 'react';

interface LaptopFormData {
  name: string;
  brand: string;
  model: string;
  price: string;
  processor: string;
  description?: string;
  category?: string;
  image: string;
  image2: string;
  usageType: string;
}

const AddLaptopForm: React.FC = () => {
  const [formData, setFormData] = useState<LaptopFormData>({
    name: '',
    brand: '',
    model: '',
    price: '',
    processor: '',
    description: '',
    category: 'Laptop',
    image: '',
    image2: '',
    usageType: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      const response = await fetch('/api/laptops', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      });
      if (response.ok) {
        alert('Laptop added successfully!');
        setFormData({
          name: '',
          brand: '',
          model: '',
          price: '',
          processor: '',
          description: '',
          category: 'Laptop',
          image: '',
          image2: '',
          usageType: ''
        });
      } else {
        alert('Error adding laptop');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="add-laptop-form">
      <input type="text" name="name" placeholder="Name" value={formData.name} onChange={handleChange} required />
      <input type="text" name="brand" placeholder="Brand" value={formData.brand} onChange={handleChange} required />
      <input type="text" name="model" placeholder="Model" value={formData.model} onChange={handleChange} required />
      <input type="text" name="price" placeholder="Price" value={formData.price} onChange={handleChange} required />
      <input type="text" name="processor" placeholder="Processor" value={formData.processor} onChange={handleChange} required />
      <input type="text" name="description" placeholder="Description" value={formData.description} onChange={handleChange} />
      <input type="text" name="category" placeholder="Category" value={formData.category} onChange={handleChange} />
      <input type="text" name="image" placeholder="Image URL" value={formData.image} onChange={handleChange} required />
      <input type="text" name="image2" placeholder="Image URL 2" value={formData.image2} onChange={handleChange} required />
      <input type="text" name="usageType" placeholder="Usage Type" value={formData.usageType} onChange={handleChange} required />
      <button type="submit">Add Laptop</button>
    </form>
  );
};

export default AddLaptopForm;
